	module.exports = {"data":{"package":{"type":"published","id":"bs-youtube","name":"bs-youtube","version":"0.0.1","category":"binding","flags":[],"platforms":["browser"],"description":"BuckleScript bindings for the youtube iframe API","keywords":["platform api","ui"],"license":"ISC","updated":"2019-01-13T23:53:18.553Z","stars":0,"score":0.42910017046309334,"quality":0.5808488459637984,"popularity":0.01306292950737394,"maintenance":0.7150671181324941,"readme":"<h1 id=\"bs-youtube\"><a href=\"#bs-youtube\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>bs-youtube</h1>\n<p>bs-youtube is a statically typed interface to the <a href=\"https://developers.google.com/youtube/iframe_api_reference\">Youtube iFrame API</a></p>\n<h2 id=\"installation\"><a href=\"#installation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Installation</h2>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-sh\"><code>npm install --save bs-youtube</code></pre></div>\n<p>In your <code>bsconfig.json</code>, include <code>\"bs-youtube\"</code> in the <code>bs-dependencies</code>.</p>\n<h2 id=\"usage\"><a href=\"#usage\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Usage</h2>\n<p>See <code>example/example.re</code> for sample usage.</p>\n\n    <div class=\"redex-codeblock m-tabbed\">\n      <ul>\n        <li class=\"reason s-selected\"> RE\n        <li class=\"ml\"> ML\n      </ul>\n      <div class=\"codeblock reason s-selected\">\n        <pre class=\"hljs lang-reason\"><code><span class=\"hljs-comment\">/*Have to init the API before doing anything else */</span>\n<span class=\"hljs-type\">Youtube</span>.<span class=\"hljs-keyword\">init</span>();\n\n<span class=\"hljs-keyword\">let</span> player = <span class=\"hljs-type\">Youtube</span>.getPlayer();\n\n<span class=\"hljs-comment\">/*To play the currently loaded video */</span>\n<span class=\"hljs-type\">Youtube</span>.playVideo(player)</code></pre>\n      </div>\n      <div class=\"codeblock ml\">\n        <pre class=\"hljs lang-ml\"><code><span class=\"hljs-keyword\">let</span> _ = <span class=\"hljs-type\">Youtube</span>.init <span class=\"hljs-literal\">()</span>\n<span class=\"hljs-keyword\">let</span> player = <span class=\"hljs-type\">Youtube</span>.getPlayer <span class=\"hljs-literal\">()</span>\n<span class=\"hljs-keyword\">let</span> _ = <span class=\"hljs-type\">Youtube</span>.playVideo player</code></pre>\n      </div>\n    </div>\n  \n<h2 id=\"development\"><a href=\"#development\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Development</h2>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-sh\"><code>npm run start</code></pre></div>\n<h2 id=\"documentation\"><a href=\"#documentation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Documentation?</h2>\n<p>See <code>example/example.re</code> for sample usage and the <a href=\"https://developers.google.com/youtube/iframe_api_reference\">Youtube API Docs</a> for the methods you can access on the player.</p>\n<h2 id=\"todo\"><a href=\"#todo\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>TODO:</h2>\n<ul>\n<li>Documentation</li>\n<li>Event listeners </li>\n<li>Events</li>\n<li>Probably refactor and make better. </li>\n<li>Test 360 Videos</li>\n</ul>\n","homepageUrl":"https://github.com/srsholmes/bs-youtube#readme","repositoryUrl":"https://github.com/srsholmes/bs-youtube","npmUrl":"https://www.npmjs.com/package/bs-youtube","issuesUrl":"https://github.com/srsholmes/bs-youtube/issues","slug":"/package/bs-youtube"}},"pathContext":{"id":"bs-youtube"}}

